.home{
    width: 100%;
    height: 90vh;
    display: flex;
    justify-content: center;
    align-items: left;
    flex-direction: column;
    background-position: center;
    background-size: cover;
    background-repeat:no-repeat;
}

.headerContainer{
    width: auto;
    margin-left: 50px;
}

.headerContainer h1{
    justify-content: center;
    align-items: center;
    font-size: 90px;
    height: 60px;
    color: #000;
}

.headerContainer p{
    padding: 0;
    font-size: 40px;
    color: #000;
}

.headerContainer button{
    border-radius: 5px;
    color:#000;
    border:none;
    cursor: pointer;
    justify-content: center;
    background-color: #121619;
    height: 50px;
    width: 180px;
    color: #fff;
    text-decoration: none;
    font-size: 20px;
}

.headerContainer button:hover{
    background-color: #07090a;
    cursor: pointer;
    transition: 0.3s ease-in;
}

@media only screen and (max-width:650px){
    .home{
        justify-content: center;
        align-items: center;
    }
    
    .headerContainer{
        margin-left: 0px;
        border-radius: 10px;
        padding: 10px;
        width:80%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        background-color: #121619;
        color:#fff;
    }
    .headerContainer h1{
        font-size: 40px;
        height: 30px;
        color: #fff;
    } 

    .headerContainer p{
        font-size:30px;
        color:#fff;
    }

    .headerContainer button{
        background-color: #fff;
        color:#121619;
        margin-bottom: 30px;
    }

    .headerContainer button:hover{
        background-color: #fff;
        color: #121619;
    }
}

